@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


/* Scrollbar Customization */

/* Scrollbar Thumb */
::-webkit-scrollbar-thumb {
  background-color: #ccc;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #aaa;
}

::-webkit-scrollbar-thumb:active {
  background-color: #bbb;
}

/* Scrollbar Track */
::-webkit-scrollbar-track {
  background-color: #fff;
  border-radius: 5px;
}

::-webkit-scrollbar-track:hover {
  background-color: #eee;
}

::-webkit-scrollbar-track:active {
  background-color: #ddd;
}

/* Scrollbar Arrows */
::-webkit-scrollbar-button {
  background-color: #fff;
  border-radius: 5px;
}

::-webkit-scrollbar-button:hover {
  background-color: #eee;
}

::-webkit-scrollbar-button:active {
  background-color: #ddd;
}

/* Scrollbar Width */
::-webkit-scrollbar {
  width: 7px;
}

/* IE11 */
.scrollbar-custom {
  -ms-overflow-style: none;
  scrollbar-width: thin;
}

.scrollbar-custom::-webkit-scrollbar {
  width: 7px;
}

.scrollbar-custom::-webkit-scrollbar-thumb {
  background-color: #ccc;
  border-radius: 5px;
}

.scrollbar-custom::-webkit-scrollbar-thumb:hover {
  background-color: #aaa;
}

.scrollbar-custom::-webkit-scrollbar-thumb:active {
  background-color: #bbb;
}

.scrollbar-custom::-webkit-scrollbar-track {
  background-color: #fff;
  border-radius: 5px;
}

.scrollbar-custom::-webkit-scrollbar-track:hover {
  background-color: #eee;
}

.scrollbar-custom::-webkit-scrollbar-track:active {
  background-color: #ddd;
}

.scrollbar-custom::-webkit-scrollbar-button {
  background-color: #fff;
  border-radius: 5px;
}

.scrollbar-custom::-webkit-scrollbar-button:hover {
  background-color: #eee;
}

.scrollbar-custom::-webkit-scrollbar-button:active {
  background-color: #ddd;
}
