.footer {
  background-color: #333;
  color: #fff;
  padding: 20px 0;
  text-align: center;
}

.footer-Heading {
  font-size: 1.5rem;
  font-weight: bold;
}

.footer-Para {
  font-size: 1rem;
  line-height: 1.5;
  margin-bottom: 10px;
}

.footer-Button {
  background-color: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  margin-bottom: 10px;
  font-weight: 700;
}

.footer-Button:hover{
  transform: scale(0.95);
}

.footer-anchorTagWithButton {
  color: #fff;
  text-decoration: none;
}



.footer-anchorTag {
  color: rgb(255, 0, 0);
  text-decoration: none;
}

h5 {
  font-size: 1rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.mt3-para {
  margin-top: 1rem;
}

.footer-Links {
  color: #fff;
  text-decoration: none;
  padding: 0 10px;
}
