/* Search Container */
.Search_container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 2rem;
  }
  
  /* Search Container Title */
  .Search_container_Title {
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
  }
  
  /* Search Container Results Length */
  .Search_container_Results_lentgh {
    text-align: center;
    font-size: 1.5rem;
    margin-top: 1rem;
    margin-bottom: 2rem;
  }
  
  /* Search Container Content */
  .Search_container_Content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  /* Search Container Content Card */
  .Search_container_Content_card {
    width: 250px;
    height: 300px;
    margin: 1rem;
    border: 1px solid #ccc;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
  }
  
  /* Search Container Content Card Image */
  .Search_container_Content_card_img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 0.5rem;
    transition: transform 0.2s ease-in-out;
  }

  .Search_container_Content_card_img:hover {
    transform: scale(0.9);
  }
  
  /* Search Container Content Card Title */
  .Search_container_Content_card_title {
    font-size: 1rem;
    font-weight: bold;
    margin: 10px;
  }
  
  /* Search Container Content Card Hover Effect */
  .Search_container_Content_card:hover {
    background-color: #eee;
  }
  