/* Homepage Title */
.Homepage_Title {
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 2rem;
  }
  
  /* Homepage Container */
  .Homepage_container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  /* Homepage Card */
  .Homepage_card {
    width: 200px;
    height: 300px;
    margin: 1rem;
    border: 1px solid #000;
    border-radius: 0.5rem;
    cursor: pointer;
  }
  
  /* Homepage Card Image */
  .Homepage_card_img {
    width: 100%;
    height: 60%;
    object-fit: cover;
    border-radius: 0.5rem;
    transition: transform 0.2s ease-in-out;
  }

  .Homepage_card_img:hover{
    transform: scale(0.9);
  }
  

  /* Homepage Card Name */
  .Homepage_card_Name {
    font-size: 1rem;
    font-weight: bold;
    margin: 10px;
  }
  
  /* Loading Container */
  .Loading_contanier {
    text-align: center;
    margin-top: 2rem;
  }
  
  /* Loading Button */
  .Loading_contanier_btn {
    width: 100px;
    height: 50px;
    background-color: #ccc;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
  }
  
  .Loading_contanier_btn:hover {
    background-color: #eee;
  }
  
  .Loading_contanier_btn span {
    font-size: 1.2rem;
    font-weight: bold;
  }
  