.form_container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-image: linear-gradient(to right, #fff, #ccc);
  }
  
  .form_title {
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
  }
  
  .form-control_divs {
    margin-top: 20px;
  }
  
  .form-control_inputs {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .form-control_inputs:focus {
    outline: none;
    border-color: #000;
  }
  
  .form_btn {
    background-color: #000;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 12px;
    text-align: center;
  }
  
  .form_para {
    text-align: center;
    margin-top: 20px;
  }
  
  .form_para_anchor_tag {
    color: #000;
    text-decoration: none;
    font-weight: bold;
  }
  