.movies_details {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 20px;
  }
  
  .movies_details_Img_Container {
    width: 100%;
    margin: 20px auto;
  }
  
  .movies_details_IMG {
    width: 100%;
    height: 300px;
    object-fit: contain;
  }
  
  .movies_details_info {
    width: 100%;
    margin: 20px auto;
  }
  
  .movies_details_info_Name {
    font-size: 2rem;
    font-weight: bold;
  }
  
  .movies_details_info_Para {
    font-size: 1.5rem;
    color: gray;
  }
  
  .movies_details_info_BTN {
    background-color: #000;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }

  .movies_details_info_BTN:hover{
    transform: scale(0.95);
  }
  
  .movies_details_info_BTN_Anchor_Tag {
    color: #fff;
    text-decoration: none;
  }

  .movies_details_info_BTN_Anchor_Tag:hover{
    color: aqua;
  }
  
  .similar_Movies {
    margin-top: 40px;
  }

  .similar_Movies_Container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  
  .similar_Movies_Heading {
    font-size: 1.5rem;
    font-weight: bold;
    margin-left: 20px;
  }
  
  .similar_Movies_NotFound {
    font-size: 1rem;
    color: gray;
  }
  
  
  .similar_Movies_card {
    width: 200px;
    margin: 20px;
    cursor: pointer;
    border: #000 1px ridge;
    border-radius: 0.5rem;
  }
  
  .similar_Movies_card_img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 0.5rem;
    transition: transform 0.2s ease-in-out;
  }

  .similar_Movies_card_img:hover{
    transform: scale(0.9);
  }
  
  .similar_Movies_card_body {
    padding: 10px;
  }

  