/* Movies Dashboard */
.movies_dashboard {
    width: 100%;
    margin: 50px auto;
    padding: 2rem;
  }
  
  /* Movies Dashboard Admin Menu */
  .movies_dashboard_AdminMenu {
    float: left;
    width: 20%;
    padding: 1rem;
  }
  
  /* Movies Dashboard Container */
  .movies_dashboard_Container {
    float: right;
    width: 80%;
  }
  
  /* Movies Dashboard Container Title */
  .movies_dashboard_Container_Title {
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
  }
  
  /* Movies Dashboard Container Movie Link Container */
  .movies_dashboard_Container_movie_link_Container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  /* Movies Dashboard Container Movie Link */
  .movies_dashboard_Container_movie_link {
    width: 200px;
    height: 300px;
    margin: 16px;
    border: 1px solid #ccc;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
  }
  
  /* Movies Dashboard Container Movie Link Card */
  .movies_dashboard_Container_movie_link_card {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  /* Movies Dashboard Container Movie Link Card Image */
  .movies_dashboard_Container_movie_link_card_img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 0.5rem;
  }
  
  /* Movies Dashboard Container Movie Link Card Title */
  .movies_dashboard_Container_movie_link_card_title {
    font-size: 1rem;
    font-weight: bold;
    margin: 10px;
  }
  
  /* Movies Dashboard Container Movie Link Hover Effect */
  .movies_dashboard_Container_movie_link:hover {
    background-color: #eee;
  }
  