/* DMCA Container */
.DMCA_Container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  /* DMCA Container Child1 */
  .DMCA_Container_Child1 {
    width: 40%;
    float: left;
    padding: 1rem;
  }
  
  /* DMCA Container Child2 */
  .DMCA_Container_Child2 {
    width: 60%;
    min-height: 100vh;
    height: min-content;
    float: right;
    padding: 1rem;
  }
  
  /* DMCA Container Title */
  .DMCA_Container_Child1 h2 {
    text-align: center;
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  /* DMCA Container Content */
  .DMCA_Container_Child2 p {
    font-size: 1rem;
  }
  
  /* DMCA Container Email */
  .DMCA_Container_Child2 .email-us {
    font-size: 1.2rem;
    font-weight: bold;
    color: #000;
    text-decoration: none;
  }
  
  /* DMCA Container Hover Effect */
  .DMCA_Container_Child2 .email-us:hover {
    color: #fff;
    background-color: #000;
  }
  